<template>
    <section ref="container">
        <a-back-top />
        <back-btn></back-btn>

        <div class="list">
            <div v-for="(info,index) in infoList" :key="info.title" class="part">
                <card1
                    v-if="info.card === 1"
                    class="card"
                    data-ani="fadeInUp"
                    :title="info.title"
                    :content="info.content"
                    :imgUrl="info.imgUrl"
                ></card1>
                <card2
                    v-if="info.card === 2"
                    data-ani="fadeInUp"
                    class="card"
                    :title="info.title"
                    :content="info.content"
                    :imgUrl="info.imgUrl"
                ></card2>
            </div>
        </div>
    </section>
</template>
<script>
import { movies, movieInfoList } from "./movieInfo";
import card1 from "../../../../base/Card/card1";
import card2 from "../../../../base/Card/card2";
export default {
    data () {
        return {
            movies,
            'infoList': movieInfoList
        };
    },
    components: { card1, card2 },
    mounted () {
        this.movies[0]['count'] = this.movies.length;
        this.sendDetails(this.movies[0]);
        window.addEventListener('scroll', this.scrollEvent, true)
        this.scrollEvent()
    },
    methods: {
        sendDetails (item) {
            this.$emit('sendMovie', item);
        },
        scrollEvent () {
            this.$nextTick(() => {
                let doms = document.querySelectorAll('.card');
                // let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
                let scrollTop = this.$refs.container.scrollTop
                let vh = document.documentElement.clientHeight;
                [].slice.call(doms).some(dom => {
                    if (scrollTop + vh > dom.offsetTop) {
                        dom.style.opacity = 1;
                        dom.classList.add(dom.dataset.ani)
                    }
                })
            })
        }
    }
};
</script>
<style lang="scss" scoped>
section {
    margin: 0;
    padding: 10px;
    height: 100vh;
    overflow: scroll;
}

.list {
    padding-bottom: 10rem;
    > .part {
        cursor: pointer;
        margin: 0 auto;
        width: 50%;
        > .card {
            opacity: 0;
            margin-top: 8rem;
        }
    }
}
.fadeInUp {
    animation: fadeInUp 1.2s ease;
}
</style>


