<template>
    <div class="card2" :style="{backgroundImage:`url(${imgUrl})`}">
        <div class="img" :style="{backgroundImage:`url(${imgUrl})`}"></div>
        <div class="title">{{title}}</div>

        <div class="card-container">
            <div class="text">{{content}}</div>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        title: {
            type: String,
            default: ''
        },
        content: {
            type: String,
            default: ''
        },
        imgUrl: {
            type: String,
            default: ''
        }
    },
    mounted () {
        // console.log(this.imgUrl)
    }
}
</script>

<style lang="scss" scoped>
.card2 {
    position: relative;
    width: 100%;
    border-radius: 6px;
    box-shadow: 0 1px 3px rgba(249, 249, 249, 0.08), 0 0 0 1px rgba(26, 53, 71, 0.04),
        0 1px 1px rgba(26, 53, 71, 0.06);
    background-size: cover;

    // background: linear-gradient(#9fcfd3 0%, #c0d4d3 12%, #fff 50%) !important;
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        background: linear-gradient(transparent 5%, #fff 60%);
    }
    > .img {
        position: absolute;
        top: -2rem;
        left: 4rem;
        width: 10rem;
        height: 13.5rem;
        z-index: 1;
        background-size: cover;
        background-position: top center;
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
        border-radius: 0.4rem;
    }
    > .title {
        position: absolute;
        top: 4rem;
        left: 2rem;
        font-size: 1.5rem;
        font-weight: 700;
        text-shadow: 0 2px 3px rgba(0, 0, 0, 0.1);
        color: #fff;
        position: relative;
        z-index: 1;
        margin-bottom: 1rem;
    }
    .card-container {
        padding: 10rem 2rem 2rem 2rem;
        font-size: 1.1rem;
        text-align: start;
        color: rgba(2, 0, 0, 0.6);

        > .text {
            position: relative;
            z-index: 1;
        }
    }
}
</style>


