<template>
    <div class="card" :style="{background:`url(${imgUrl})`}">
        <div class="card-container">
            <div class="title">{{title}}</div>
            <div class="text">{{content}}</div>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        title: {
            type: String,
            default: ''
        },
        content: {
            type: String,
            default: ''
        },
        imgUrl: {
            type: String,
            default: ''
        }
    },
    mounted () {
        // console.log(this.imgUrl)
    }
}
</script>

<style lang="scss" scoped>
.card {
    position: relative;
    width: 100%;
    border-radius: 6px;
    box-shadow: 0 1px 3px rgba(249, 249, 249, 0.08), 0 0 0 1px rgba(26, 53, 71, 0.04),
        0 1px 1px rgba(26, 53, 71, 0.06);
    background: #fff;
    background-repeat: no-repeat !important;
    background-size: contain !important;
    background-position: right center !important;
    .card-container {
        padding: 2rem 12rem 2rem 2rem;
        font-size: 1.1rem;
        text-align: start;
        color: rgba(2, 0, 0, 0.6);
        > .title {
            font-size: 1.4rem;
            font-weight: 600;
            color: rgb(17, 17, 17);
            position: relative;
            z-index: 1;
            margin-bottom: 1rem;
        }
        > .text {
            position: relative;
            z-index: 1;
        }
        &:after {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            z-index: 0;
            top: 0;
            left: 0;
            background: -webkit-linear-gradient(right, rgba(255, 255, 255, 0) 0%, #fff 20%);
            background: linear-gradient(to left, rgba(255, 255, 255, 0) 0%, #fff 20%);
        }
    }
}
</style>


